@font-face {
  font-family: 'icomoon';
  src:  url('../fonts/icomoon.eot?gsyx2e');
  src:  url('../fonts/icomoon.eot?gsyx2e#iefix') format('embedded-opentype'),
    url('../fonts/icomoon.ttf?gsyx2e') format('truetype'),
    url('../fonts/icomoon.woff?gsyx2e') format('woff'),
    url('../fonts/icomoon.svg?gsyx2e#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  
  /* Enable Ligatures ================ */
  letter-spacing: 0;
  -webkit-font-feature-settings: "liga";
  -moz-font-feature-settings: "liga=1";
  -moz-font-feature-settings: "liga";
  -ms-font-feature-settings: "liga" 1;
  font-feature-settings: "liga";
  -webkit-font-variant-ligatures: discretionary-ligatures;
  font-variant-ligatures: discretionary-ligatures;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-close:before {
  content: "\e900";
}
.icon-search:before {
  content: "\e901";
}
.icon-thumbs-down:before {
  content: "\e902";
}
.icon-thumbs-up:before {
  content: "\e903";
}
.icon-menu:before {
  content: "\e9bd";
}
.icon-facebook2:before {
  content: "\ea91";
}
.icon-twitter:before {
  content: "\ea96";
}
